.lookbook__imgContainer {
  overflow: hidden;
  position: relative !important;
  margin-top: 0px !important;
}
.lookbook__firstImg,
.lookbook__secondImg {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: all 1s;
  transition-property: all;
  transition-duration: 1s;
  transition-timing-function: ease;
  transition-delay: 0s;
}
/*  */

/*  */
.lookbook__firstImg:hover {
  -webkit-transform: rotate(15deg) scale(1.4);
  transform: rotate(15deg) scale(1.4);
  transition: all 1s;
  transition-property: all;
  transition-duration: 1s;
  transition-timing-function: ease;
  transition-delay: 0s;
  cursor: pointer;
}

.lookbook__secondImg:hover {
  transform: scale(1.3);

  transition-property: all;
  transition-duration: 1s;
  transition-timing-function: ease;
  transition-delay: 0s;
  cursor: pointer;
}

.lookBook__slideBtn {
  position: absolute !important;
  top: 83%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: none !important;
  background-color: #fff;
  color: black;
  font-size: 14px;
  padding: 10px 40px;
  border: none !important;
  font-weight: 600;
  border: 0;
  text-decoration: none !important;

  cursor: pointer;
  transition: all 1s;
  /* border sharpt edge */
}
.mainContainer {
  background-color: red;
}
.lookBook__text1 {
  position: absolute;
  top: 42%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 20px;
  color: white;
}

