.productpage__collection {
  opacity: 0.75;

  position: relative;
  height: 180px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("https://cdn.shopify.com/s/files/1/0616/9480/4174/files/shop-banner.jpg");
  background-size: cover;
}

.productpage__collection::before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-color: rgba(0, 0, 0, 0.25);
}
.productpage__collection h5 {
  position: relative;
  color: #ffffff;
  line-height: 0.9;
  text-align: center;
}
.productpage__collection p {
  display: block;
  position: relative;
  color: #ffffff;
  line-height: 0.9;
  text-align: center;
}

@media only screen and (max-width: 768px) {
  .productproductpage__collection {
    height: 110px;
  }
  .productpage__collection p {
    background-color: green;
  }
}
.filter__btnText,
.filter__btnImg {
  transition: all 0.3s;
}
.filter__btn:hover .filter__btnImg {
  color: #56cfe1;
  cursor: pointer;
}
.filter__btn:hover .filter__btnText {
  color: #56cfe1;
  cursor: pointer;
}
/* sorting button style */

.selectWrapper {
  border-radius: 36px;
  display: inline-block;
  overflow: hidden;
  background: #cccccc;
  border: 1px solid #cccccc;
}
.selectBox {
  width: 140px;
  height: 40px;
  border: 0px;
  outline: none;
}

