.select-box {
  position: relative;
  display: block;
  width: 100%;
  margin: 0 auto;
  font-family: "Open Sans", "Helvetica Neue", "Segoe UI", "Calibri", "Arial",
    sans-serif;
  font-size: 18px;
  color: #60666d;
}
@media (min-width: 768px) {
  .select-box {
    width: 70%;
  }
}
@media (min-width: 992px) {
  .select-box {
    width: 50%;
  }
}
@media (min-width: 1200px) {
  .select-box {
    width: 30%;
  }
}
.select-box__current {
  position: relative;
  box-shadow: 0 15px 30px -10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  outline: none;
}
.select-box__current:focus + .select-box__list {
  opacity: 1;
  -webkit-animation-name: none;
  animation-name: none;
}
.select-box__current:focus + .select-box__list .select-box__option {
  cursor: pointer;
}
.select-box__current:focus .select-box__icon {
  transform: translateY(-50%) rotate(180deg);
}
.select-box__icon {
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  width: 20px;
  opacity: 0.3;
  transition: 0.2s ease;
}
.select-box__value {
  display: flex;
}
.select-box__input {
  display: none;
}
.select-box__input:checked + .select-box__input-text {
  display: block;
}
.select-box__input-text {
  display: none;
  width: 100%;
  margin: 0;
  padding: 15px;
  background-color: #fff;
}
.select-box__list {
  position: absolute;
  width: 100%;
  padding: 0;
  list-style: none;
  opacity: 0;
  -webkit-animation-name: HideList;
  animation-name: HideList;
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  -webkit-animation-timing-function: step-start;
  animation-timing-function: step-start;
  box-shadow: 0 15px 30px -10px rgba(0, 0, 0, 0.1);
}
.select-box__option {
  display: block;
  padding: 15px;
  background-color: #fff;
}
.select-box__option:hover,
.select-box__option:focus {
  color: #546c84;
  background-color: #fbfbfb;
}

@-webkit-keyframes HideList {
  from {
    transform: scaleY(1);
  }
  to {
    transform: scaleY(0);
  }
}

@keyframes HideList {
  from {
    transform: scaleY(1);
  }
  to {
    transform: scaleY(0);
  }
}

.form-select {
  width: 300px;
  background-color: white;
  border-radius: 50px;
  height: 40px;
  width: 230px;
  padding: 5px;
}

.form-select:focus-visible {
  /* remove decoration */
  outline: none;
  /* add border */
}

/* group btn design */

.btn__minus {
  /* make btn__minus button round from left side */
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
  margin: 0px;
  display: inline-block;
}
.btn__input {
  margin: 0px;
  padding: 0px;
  display: inline-block;
}
.btn__plus {
  display: inline-block;
  margin: 0px;
}
.addtocart__btn {
  display: inline-block;
  border-radius: 0px 50px 50px 0px;
  border: none;
  outline: none;
  padding-top: 3px;
  padding-bottom: 3px;
}
.btn__minus,
.btn__plus,
.btn__input {
  background-color: #ddd !important;
  outline: none;
  text-decoration: none;
  padding-top: 3px;
  padding-bottom: 3px;

  border: none;
}
.btn__minus:hover,
.btn__plus:hover,
.btn__input:hover {
  background-color: #cecece !important;
}

.btn__input {
  width: 26px;
  padding-left: 2px;
}

.btn__minus:focus-visible,
.btn__minus:focus,
.btn__plus:focus-visible,
.btn__plus:focus,
.btn__input:focus-visible,
.btn__input:focus {
  outline: none;
}

.addtocart__btn:focus-visible,
.addtocart__btn:focus {
  outline: none !important;
}

