.pagination__container {
  display: flex;
  margin-left: 40%;
  margin-right: 40%;
  margin-bottom: 25px;
  justify-content: space-between;
}
.pagination__container p {
  font-size: 12px;
  color: #878787;
}
.pagination__container p:hover {
  color: red;
  cursor: pointer;
}

p.pagination__active {
  color: red;
}
p.dot-dot:hover {
  cursor: default;
  color: #878787;
}

/*  Back to Top */
#btn-back-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: none;
  background-color: #fff;
  color: #878787;
  transition: all 0.4s ease 0s;
  border: #878787 1px solid;
}
#btn-back-to-top:hover {
  background-color: #56cfe1;
  color: white;
  z-index: 100;
  border: #56cfe1 1px solid;
}
#btn-back-to-top:focus-visible {
  outline: none;
}
