/* design background */
@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");

* {
  list-style: none;
  text-decoration: none;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
}
/* .active {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
} */
.wrapper {
  z-index: 10;
  background: none;
  z-index: 468;
  opacity: 1;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}
.wrapper .sidebar {
  background: rgb(255, 255, 255);
  position: fixed;
  top: 0;
  left: 0;
  width: 270px;
  height: 100%;
  margin: 0px;

  transition: all 0.5s ease;
  z-index: 500;
}

.menu__heading {
  font-size: 16px;
  font-weight: 500;
  display: flex;
  justify-content: space-between !important;
}
.menu__heading p {
  position: relative;
  top: 10px;
  left: 5px;
}

filter-hidden.t4s-drawer .t4s-drawer__header button.t4s-drawer__close {
  font-size: 16px;
  font-weight: 500;
  background-color: transparent;
}
button.t4s-drawer__close {
  padding: 0px;
  background-color: transparent;
  color: var(--t4s-dark-color);
  width: 14px;
  height: 37px;
  margin-right: 18px;
  outline: none;
  rotate: 0deg;
  transition: all 0.6s ease;
  font-weight: bolder;
  border: none;
}

button.t4s-drawer__close:hover {
  /* rotate icon 90 deg */
  transform: rotate(180deg);
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 0.3px solid #878788;
  padding: 0px 0px;
  opacity: 0.3;
}

.available__belowBorder {
  border-bottom: 1.5px solid black;
  width: 20%;
}
.product__belowBorder {
  border-bottom: 1.5px solid black;
  width: 24%;
}
input.custom__checkbox {
  height: 10px;
  width: auto !important;
}

/* input {
  width: auto !important;
} */

.first__checkbox {
  margin-top: 16px;
  display: flex;
  justify-content: start;
  height: 8px;
}

.product__blockSrollDiv {
  height: 221px;
  overflow-y: auto;
  overflow-x: hidden;
}
.sidebar__mainScroll {
  height: 90vh;
  overflow-y: auto;
  overflow-x: hidden;
}
.first__checkbox p {
  margin-left: 10px;
  align-self: center;
  font-size: 12px;
  margin-top: 14px;
  color: #878787;
  transition: all 0.3s ease-in-out;
}
.first__checkbox input {
  align-self: center;
}
.first__checkbox input::after {
  background-color: #56cfe1 !important;
  color: white;
}
.first__checkbox p:hover {
  cursor: pointer;
  color: #56cfe1;
}

.sidebar__mainScroll::-webkit-scrollbar {
  width: 3px;
}
.product__blockSrollDiv::-webkit-scrollbar {
  width: 3px;
}

.sidebar__mainScroll::-webkit-scrollbar-track {
  background: #f2f2f2;
  /* border-radius: 5px; */
}
.product__blockSrollDiv::-webkit-scrollbar-track {
  background: #f2f2f2;
  border-radius: 12px;
}
.sidebar__mainScroll::-webkit-scrollbar-thumb {
  background: #d5d5d5;
  /* border-radius: 3px; */
}

.product__blockSrollDiv::-webkit-scrollbar-thumb {
  background: #d5d5d5;
  /* border-radius: 3px; */
}

.hideSidebar {
  display: none;
  visibility: hidden;
  cursor: pointer;
}
