/* hover fill button from bottom to top */
.slide__btn {
  padding: 10px 20px 10px 20px;
  display: inline-block;
  position: relative;
  text-decoration: none;
  font-weight: bold;
  font-size: 15px;
  background-color: #000;
  background-image: linear-gradient(to top, #fff 50%, #fff 50%),
    linear-gradient(to top, #56cfe1 50%, transparent 50%);
  /* remove shadow */
  box-shadow: none;
  -webkit-background-clip: text, padding-box;
  background-clip: text, padding-box;
  -webkit-text-fill-color: transparent;
  color: transparent;
  background-size: 100% 200%;
  background-position: top;
  transition: background-position 0.5s ease-in-out;
  border: none;
  outline: none;
  text-decoration: none !important;
}

.slide__btn:hover {
  background-position: bottom;
  border: none;
  outline: none;
  text-decoration: none !important;
}
.carousel-item {
  height: 100% !important;
}
/* animation  */
/* first carousel */
.slider__imgTextFirst {
  animation: moveup 1s linear 1;
  /* name duration aninmation-timing-function animation-delay */

  visibility: hidden;
  animation-fill-mode: forwards;
  animation-delay: 0s;
  animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}
@keyframes moveup {
  0% {
    transform: translateY(10px);
    visibility: visible;
    opacity: 0.5;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
  }
}
/* second text */
.slider__imgTextSecond {
  animation: moveup 1s ease-in-out 1;
  visibility: hidden;
  animation-fill-mode: forwards;
  animation-delay: 0.8s;
  animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}
@keyframes moveup {
  0% {
    transform: translateY(20px);
    visibility: visible;
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
  }
}
/* 3rd text */
