.Header {
  margin: 0;
  padding: 0;
}
.header__heading {
  margin: 0;
  padding: 0;
}

.top-header {
  background-color: #f6f6f8;
  padding: 10px 10px;
  font-family: "Poppins";
  min-height: 45px;
}

.navbar-nav .nav-item .nav-link {
  color: #878787;
  transition: all 0.3s ease-in-out;
}
.navbar-nav .nav-item.active .nav-link,
.navbar-nav .nav-item:hover .nav-link {
  color: #56cfe1;
  transition: all 0.3s ease-in-out;
}
