.summer__heading {
  color: #878787;
}
.header__svg {
  margin-bottom: 5px;
  margin-right: 2px;
}
.header__locationSvg {
  margin-bottom: 4px;
}
.shop__now {
  color: #222222;
  transition: all 0.3s ease-in-out;
}

.header__arrowSvg {
  margin-bottom: 2px;
}
.header__arrowSvgCurrency ::before {
  background-image: url("https://cdn.shopify.com/s/files/1/0616/9480/4174/t/2/assets/cad.svg?19041994");
}
.header__select {
  border: none;
  border-radius: 5px;
  padding: 5px;
  /* width: 100px;
  height: 30px; */
  font-size: 15px;
  color: #878787;
  background-color: #f6f6f8;
  transition: all 0.3s ease-in-out;
  border: none !important;
  outline: none !important;
  margin-right: 0px;
}
