.collection__firstDivImg,
.collection__secondDivImgUp,
.collection__secondDivImgDown,
.collection__thirdDivImg {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: all 1s;
  transition-property: all;
  transition-duration: 1s;
  transition-timing-function: ease;
  transition-delay: 0s;
}

/* scale on hover */
.collection__imgContainer:hover img {
  transform: scale(1.1);
  transition: all 1s;
  transition-property: all;
  transition-duration: 1s;
  transition-timing-function: ease;
  transition-delay: 0s;
}
.collection__imgContainer {
  overflow: hidden;
  position: relative !important;
  height: 100%;
}

/* button  */
.collection__slideBtnAccesories,
.collection__slideBtnFootwear,
.collection__slideBtnWatch,
.collection__slideBtnWomen {
  position: absolute !important;
  top: 83%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: none !important;
  background-color: #fff;
  color: black;
  font-size: 14px;
  padding: 10px 40px;
  border: none !important;
  font-weight: 600;
  border: 0;
  text-decoration: none !important;

  cursor: pointer;
  transition: all 1s;
  /* border sharpt edge */
}

.collection__slideBtnAccesories:hover,
.collection__slideBtnFootwear:hover,
.collection__slideBtnWomen:hover,
.collection__slideBtnWatch:hover {
  background-color: black;
  color: #fff;
  transition: all 1s;
}
.collection__slideBtn:focus,
.collection__slideBtnWomen:focus,
.collection__slideBtnWatch:focus {
  outline: none;
  text-decoration: none !important;
}

.collection__slideBtnWomen,
.collection__slideBtnWatch {
  position: absolute !important;
  top: 92%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: none !important;
  background-color: #fff;
  color: black;
  font-size: 14px;
  padding: 10px 40px;
  border: none !important;
  font-weight: 600;
  border: 0;
  text-decoration: none !important;
  cursor: pointer;
  transition: all 1s;
  /* border sharpt edge */
}
