/*rounded button */
/* make */

input {
  margin: 0px;
  width: 100%;
  outline: none;
  height: 50px;
  border-radius: 46px;
  border-width: 1px;
  border-style: solid;
  border-color: #000000;
  padding: 15px;
  margin-left: 0;
  margin-right: 0;
  display: flex;
  align-items: center;
}
/* place holder css */

.footer__btn2,
.footer__btn {
  position: absolute;
  top: 0;
  border-radius: 5px;
  right: 0px;
  z-index: 2;
  border: none;
  top: 2px;
  height: 43px;
  cursor: pointer;
  color: white;
  background-color: #1e90ff;
  transform: translateX(2px);
  margin-right: 5px;
  margin-top: 1px;
}
.footer__btn {
  -webkit-border-radius: 28;
  -moz-border-radius: 28;
  border-radius: 28px;
  font-family: Arial;
  color: #ffffff;
  font-size: 20px;
  background: #222222;
  padding: 7px 20px 10px 20px;
  text-decoration: none;
}

.footer__footer__btn:hover {
  background: #56cfe1;

  text-decoration: none;
  color: #ffffff;
}

.soical__icon {
  color: #878787 !important;
}
.t4s-socials a svg {
  margin: 400px;
  fill: #878787 !important;
  display: inline-block;
  vertical-align: middle;
  border: none;
}

.t4s-icon-twitter,
.t4s-icon-facebook,
.t4s-icon-instagram,
.t4s-icon-linkedin,
.t4s-icon-pinterest {
  fill: #878787;
  margin-top: 3px !important;
  margin-right: 20px !important;
  position: relative;
  bottom: 0px;
  transition: bottom 0.5s ease-in-out;

  /* transition: all 0.5s ease; */
}
/* hover on all icon */
.t4s-icon-twitter:hover,
.t4s-icon-facebook:hover,
.t4s-icon-instagram:hover,
.t4s-icon-linkedin:hover,
.t4s-icon-pinterest:hover {
  cursor: pointer;
  position: relative;
  bottom: 10px;
}
.t4s-icon-twitter:hover {
  fill: #1e90ff;
}

.t4s-icon-facebook:hover {
  fill: #3b5998;
}
.t4s-icon-instagram:hover {
  fill: #e1306c;
}
.t4s-icon-linkedin:hover {
  fill: #0077b5;
}
.t4s-icon-pinterest:hover {
  fill: #cb2027;
}

/* desktop*/
@media only screen and (min-width: 992px) {
  .social__iconContainer {
    margin-right: 100px;
    margin-top: 7px;
  }

  .buttonIn {
    margin-bottom: 12px;
    width: 300px;
    position: relative;
  }
  /* .buttonIn input {
    padding-right: 143px !important;
  } */
}
/* tablet */
@media only screen and (min-width: 768px) and (max-width: 991px) {
  .buttonIn {
    margin-bottom: 12px;
    width: 300px;
    position: relative;
  }
  .social__iconContainer {
    margin-right: 100px;
    margin-top: 7px;
  }
}
/* mobile */
@media only screen and (max-width: 768px) {
  .buttonIn {
    margin-bottom: 12px;
    width: 300px;
    position: relative;
  }

  .footer__columns {
    margin-top: 20px !important;
    font-weight: 1000px;
  }

  .t4s-footer-collapse-icon:before {
    width: 12px;
    height: 1px;
    opacity: 1;
  }
  .t4s-footer-collapse-icon:after {
    width: 1px;
    height: 12px;
  }

  .t4s-footer-collapse-icon:before {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(-90deg);
    background-color: currentColor;
    transition: transform 0.35s ease-in-out, opacity 0.35s ease-in-out,
      -webkit-transform 0.35s ease-in-out;
  }
  .t4s-footer-collapse-icon:after {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    background-color: currentColor;
    transition: transform 0.35s ease-in-out, opacity 0.35s ease-in-out,
      -webkit-transform 0.35s ease-in-out;
  }
}

/* plus minus button */

.footer__dropdownBtn {
  color: #878787;
  background: none !important;
  width: 50px;
  height: 50px;
  border: 0;
  font-size: 1.5em;
  position: relative;
}

.footer__dropdownBtn .footer__dropdownSpan {
  position: absolute;
  transition: 300ms;
  background: #878787;
  border-radius: 2px;
}

/* Create the "+" shape by positioning the spans absolutely */
.footer__dropdownBtn .footer__dropdownSpan:first-child {
  top: 25%;
  bottom: 25%;
  width: 10%;
  left: 45%;
}

.footer__dropdownBtn .footer__dropdownSpan:last-child {
  left: 25%;
  right: 25%;
  height: 10%;
  top: 45%;
}

/* Morph the shape when the button is hovered over */
.footer__dropdownBtn:active .footer__dropdownSpan {
  transform: rotate(90deg);
}

.footer__dropdownBtn:active .footer__dropdownSpan:last-child {
  left: 50%;
  right: 50%;
}

.copyright__left,
.copyright__right {
  color: #878787;
