}
/* custome__togglerBtn */

.custome__togglerBtn {
  color: #878787;
  background: transparent;
  width: 70px;
  height: 70px;
  position: relative;
  font-size: 50px;
  cursor: pointer;
  border: 0;
  outline: 0;
  padding: 0;
  top: 33px;
  left: 57px;
  transform: translate(-50%, -50%) rotate(-90deg);
  transform: 0.5s ease-in-out, opacity 0.35s ease-in-out;
}
.plus,
.minus {
  /* color: #fff; */
  padding: 10px;
  width: 70px;
  height: 70px;
  line-height: 50px;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  box-sizing: border-box;
  transition: 0.5s all ease-out;
  font-size: 15px;
  color: #878787 !important;
}
.plus {
  opacity: 1;
  transform: rotate(0deg);
}
.custome__togglerBtn.active .plus {
  opacity: 0;
  transform: rotate(90deg);
}
.minus {
  opacity: 0;
  transform: rotate(-90deg);
}
.custome__togglerBtn.active .minus {
  opacity: 1;
  transform: rotate(90deg);
}

.custome__togglerBtn:focus {
  outline: none !important;
}
