@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap");

.main-wrapper img {
  width: 100%;
  display: block;
}
.main-wrapper a {
  color: #000;
  text-decoration: none;
}
.main-wrapper ul li {
  list-style-type: none;
}

.drop-icon {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}
.sub-menu h4 {
  text-transform: capitalize;
  font-size: 1rem;
  padding: 0.5rem 0;
}
.sub-menu ul li {
  text-transform: capitalize;
  padding: 0.2rem 0;
  margin: 0.2rem 0;
  font-size: 0.95rem;
}
.sub-menu ul li a {
  opacity: 0.8;
  transition: all 0.5s ease;
}
.sub-menu ul li a:hover {
  padding-left: 14px;
  opacity: 0.9;
}

.sub-menu-item {
  padding-left: 1.2rem;
}
.sub-menu-item:nth-child(3) {
  background: #ddd;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.sub-menu-item:nth-child(3) h2 {
  text-transform: capitalize;
  margin: 1.5rem 0;
}
.sub-menu-item:nth-child(3) .btn {
  border: 1px solid #000;
  text-transform: uppercase;
  font-size: 0.9rem;
  padding: 0.6rem 1rem;
  cursor: pointer;
  background: #000;
  color: #fff;
  transition: all 0.5s ease;
}
.sub-menu-item:nth-child(3) .btn:hover {
  background: transparent;
  color: #000;
}
.sub-menu-item:nth-child(4) {
  width: 50%;
  margin: 0 auto;
  padding: 2rem 0;
}
.demo__dropdownImg {
  width: 160px;
  height: 160px;
}
.demo__mainRow {
  display: flex;
  justify-content: space-around !important;
}

.demo__dropdownImg {
  margin: 10px;
  transition: 0.3s ease-in-out;
  box-shadow: 50px green;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
}

.demo__dropdownP {
  margin: 0;
  font-size: 14px;
  font-weight: bolder;
  color: black;
}
.demo__dropdownImgContainer:hover img {
  /* lift img from bottom */
  transform: translateY(-5px);
  cursor: pointer;
}

.demo__dropdownP {
  font-size: 10px;
  text-align: center;
}

.navbar {
  padding: 0px !important;
}
.nav-link {
  padding: 15px 10px !important;
}

.sub-menu {
  display: none;
}
.demo:hover .sub-menu {
  background-color: white;
  display: block;
  position: absolute;
  left: -14px;
  top: 54px;
  width: 100vw;
  height: 78vh;
  z-index: 9999999;
}
.sub-menu {
  border-top: #efefef 0.2px solid;
}
