.btn-group {
  position: relative;
}

.cart__inputField {
  /* padding-top: 10; */
  height: 35px;
  width: 104px;
  /* padding: 10px 45px; */
  text-align: center;
}

.cart__plusbtn {
  position: absolute;
  top: -5px;
  right: 0;
  color: black;
  border: none;
  height: 23px;
  width: 40px;
  display: table-cell;
  vertical-align: middle;
  font-size: 28px;
  background-color: transparent;
  transition: all 0.3s ease-in-out;
}
.cart__minusbtn {
  position: absolute;
  top: -5px;
  left: 0;
  color: black;
  border: none;
  height: 50px;
  width: 35px;
  display: table-cell;
  vertical-align: middle;
  font-size: 26px;
  background-color: transparent;
  transition: all 0.3s ease-in-out;
}
.btn-group button:focus-visible,
.btn-group button:focus {
  outline: none;
}

.btn-group button:hover {
  transition: all 0.3s ease-in-out;
  color: #23b2c7;
}
.cart__minusbtn .icon__delete {
  top: -10px;
  font-size: 15px;
}
