/* heading */
@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap");
.trending__headingFirst h2 {
  display: flex;
  flex-direction: row;
  font-size: 24px;
  font-weight: 600;
  line-height: 34px;
}
.trending__headingFirst h2:before,
.trending__headingFirst h2:after {
  content: "";
  flex: 0.09 0.09;
  border-bottom: 2px solid;
  width: 20px;
  margin: auto;
}
.trending__headingFirst h2:before {
  margin-right: 25px;
}
.trending__headingFirst h2:after {
  margin-left: 25px;
}

.trending__headingSecond {
  color: #878787;
  font-style: italic;
  font-size: 14px;
  font-weight: 400;

  font-family: "Libre Baskerville", serif;
}

/* hover fill button from bottom to top */
.outer {
  margin: 50px;
}

.button {
  border: 1px solid black;
  border-radius: 3px;
  width: 100px;
  height: 30px;
  display: block;

  background: linear-gradient(to right, black 50%, white 50%);
  background-size: 200% 100%;
  background-position: right bottom;
  transition: all 0.5s ease-out;
}

.button:hover {
  background-position: left bottom;
}

.text {
  text-align: center;
  font-size: 16px;
  line-height: 30px;
  color: black;
  transition: all 0.6s ease-out;
  display: block;
}

.text:hover {
  color: white;
}
.trending__btnContainer {
  position: relative;
  margin-bottom: 20px !important;
  margin-top: 40px;
}

.trending__btnContainer .slide__btn {
  text-align: center;
  display: inline-block;
}
/* load more btn */

.col-6 {
  padding-right: 15px;
  padding-left: 5px;
}

@media only screen and (max-width: 768px) {
  .load__moreBtn {
    font: 11px Arial !important;
    display: inline-block;
    padding: 7px 24px !important;
    text-align: center;
    border: 2px solid black;
    border-radius: 50px;
    color: black;
    /* font-weight: bold; */
    background: red;
    background: linear-gradient(to left, #fff 50%, #00badb 50%) right;
    background-size: 200%;
    transition: 0.5s ease-out;
    text-align: center;
  }

  .load__moreBtn:hover {
    background-position: left;
    color: white;
    cursor: pointer;
    border-color: #00badb;
  }
  .load__moreBtnContainer {
    text-align: center;
  }
}

@media only screen and (min-width: 769px) {
  .load__moreBtn {
    font: 14px Arial !important;
    display: inline-block;
    padding: 8px 29px !important;
    text-align: center;
    border: 2px solid black;
    border-radius: 50px;
    color: black;
    /* font-weight: bold; */
    background: red;
    background: linear-gradient(to left, #fff 50%, #00badb 50%) right;
    background-size: 200%;
    transition: 0.5s ease-out;
    text-align: center;
  }

  .load__moreBtn:hover {
    background-position: left;
    color: white;
    cursor: pointer;
    border-color: #00badb;
  }
  .load__moreBtnContainer {
    text-align: center;
  }
}
