.shop__collection {
  opacity: 0.88;

  position: relative;
  height: 130px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("https://cdn.shopify.com/s/files/1/0616/9480/4174/files/bg-heading.jpg?v=1652069957&width=1512");
  background-size: cover;
}

.shop__collection::before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-color: rgba(0, 0, 0, 0.25);
}
h5 {
  position: relative;
  color: #ffffff;
  line-height: 0.9;
  text-align: center;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 768px) {
  .shop__products {
    margin: 0px 7px;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .shop__products {
    margin: 0px 12px;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .shop__products {
    margin: 0px 20px;
  }
}

