.product__parent {
  position: relative;
}

.product__heading,
.product__heading img {
  color: white !important;
  fill: aqua;
}

.product__body {
  overflow: hidden;
  position: relative !important;
  height: auto;

  width: 100%;
  margin: 5px;
}
.product__parent {
  position: relative;
}
/* child */
.product__body {
  transition: ease-in-out 1s;
  overflow: hidden;
}
.product__imgContainer {
  /* transition: ease-in-out 1s; */
  overflow: hidden;
}

.product__body:hover .product__bodyImg1 {
  transform: scale(1.1);
  transition: all 1s;

  cursor: pointer;
}
.product__body:hover .product__bodyImg2 {
  transform: scale(1.1);
  transition: all 1s;
  cursor: pointer;
}
.product__bodyImg1 {
  height: 100%;
  width: 100%;
  object-fit: cover;
  display: block;
  transition: ease-in-out 1s;
}

.product__bodyImg2 {
  height: 100%;
  width: 100%;
  object-fit: cover;
  display: block;
  transition: ease-in-out 1s;
  z-index: 1;
}

/* center btn design */

/* icon in heading */

/* animation for btn and footer size */
.product__body:hover .product__btn1 {
  opacity: 1;
  top: 55%;
}

.product__btn1:hover {
  content: "";
  background-color: black;
  background-size: 22px 18px !important;
  background-position: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  content: "";
  color: white;
  /* display: none; */
}

