/* product 2 design css */

.product__btn2 {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  background-color: #fff;
  color: black;
  font-size: 14px;
  padding: 6px 25px;
  border-radius: 40px;
  font-weight: 600;
  border: 0;
  text-decoration: none !important;
  cursor: pointer;
  transition: all 1s;
  z-index: 3;
  transition: 0.5s ease 0s;
  height: 35px;
  width: 140px;
  overflow: hidden;

  transition: all 2s cubic-bezier(0.215, 0.61, 0.355, 1);
  opacity: 0;
}

.product__btn1 {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  background-color: #fff;
  color: black;
  font-size: 14px;
  padding: 6px 25px;
  border-radius: 40px;
  font-weight: 600;
  border: 0;
  text-decoration: none !important;
  cursor: pointer;
  transition: all 1s;
  z-index: 3;
  transition: 0.5s ease 0s;
  height: 35px;
  width: 140px;
  overflow: hidden;

  transition: all 2s cubic-bezier(0.215, 0.61, 0.355, 1);
  opacity: 0;
}

.product__btn2::before {
  content: "\f06e";
  font-family: FontAwesome;
  position: absolute;
  top: 100%;
  left: 0;
  height: 100%;
  width: 100%;
  padding-top: 6px;
  border-radius: 50px;
  color: #ffffff;
  background-color: #000;
  transition: ease-in-out 0.3s;
  z-index: 6;
}

.product__btn1::before {
  content: "\f07a";
  font-family: FontAwesome;
  position: absolute;
  top: 100%;
  left: 0;
  height: 100%;
  width: 100%;
  padding-top: 7px;
  border-radius: 50px;
  color: #ffffff;
  background-color: #000;
  transition: ease-in-out 0.3s;
  z-index: 6;
}

.product__btn2:hover::before {
  top: 0;
}

.product__btn1:hover::before {
  top: 0;
}

.product__btn2::after {
  content: "Quick View";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 100%;
  padding-top: 6px;
  border-radius: 50px;
  color: #000;
  background-color: #fff;
  transition: ease-in-out 0.3s;
  z-index: 5;
}
.product__btn1::after {
  content: "Add to cart";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 100%;
  padding-top: 6px;
  border-radius: 50px;
  color: #000;
  background-color: #fff;
  transition: ease-in-out 0.3s;
  z-index: 5;
}

.product__btn2:hover::after {
  bottom: 100%;
}

.product__btn1:hover::after {
  bottom: 100%;
}
.product__btn2:focus {
  border: none;
  outline: none;
}
.product__btn1:focus {
  border: none;
  outline: none;
}

.product__body:hover .product__btn2 {
  opacity: 1;
  top: 40%;
}
.product__body:hover .product__btn1 {
  opacity: 1;
  top: 55%;
}

/* heart beat  */
.product__icon img:hover {
  cursor: pointer;
  animation: beat 0.35s infinite alternate;
  transform-origin: center;
}

/* Heart beat animation */
@keyframes beat {
  to {
    transform: scale(1.2);
  }
}

.product__icon:first-child {
  margin-bottom: 10px;
}

/* animation */
.product__icon {
  position: absolute;
  top: 3%;
  left: -22px;
  z-index: 1;
  transition: all 1s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.product__body:hover .product__icon {
  left: 15px;
}
.nohover:hover {
  /* do nothing */
  color: #878787;
  cursor: none;
}

.product__pricing p span {
  cursor: pointer !important;
}

@media only screen and (max-width: 768px) {
  .product__pricing h6 {
    font-size: 9px;
  }
  .product__pricing p {
    font-size: 8px;
    cursor: pointer;
  }

  .product__pricing p:hover {
    cursor: pointer;
  }

  .product__btn1 {
    width: 100px !important;
    height: 24px !important;
    font-size: 8px;
  }
  .product__btn2 {
    width: 100px !important;
    height: 24px !important;
    font-size: 8px;
  }

  /* footer  */
  .product__footer {
    font-size: 12px;
    position: absolute;
    top: 60% !important;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    z-index: 1;
    opacity: 0;
    transition: top 2s cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  .product__body:hover .product__footer {
    opacity: 1;
    top: 78% !important;
  }
}

@media only screen and (min-width: 768px) {
  .product__pricing h6 {
    font-size: 16px;
  }
  .product__pricing p {
    font-size: 12px;
    cursor: pointer;
  }
  .product__pricing p:hover {
    cursor: pointer;
  }
  .product__btn1 {
    width: 128px !important;
    height: 34px !important;
    font-size: 14px;
  }
  .product__btn2 {
    width: 128px !important;
    height: 34px !important;
    font-size: 14px;
  }

  /* footer  */
  .product__footer {
    position: absolute;
    top: 50% !important;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    z-index: 1;
    opacity: 0;
    transition: top 2s cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  .product__body:hover .product__footer {
    opacity: 1;
    top: 78% !important;
  }
}
